// @flow
import * as React from 'react';
import clsx from 'clsx';
import {
  AppBar,
  makeStyles,
  Container,
  ListItem,
  ListItemIcon,
  ListItemText,
  Grid,
} from '@material-ui/core';
import { NavLink } from 'react-router-dom';
import { ShowChart, HomeWork, FileCopy } from '@material-ui/icons';
import _get from 'lodash/get';
import { Auth } from 'aws-amplify';
import imageLogo from '../../images/logo_n.svg';
import style from './headerStyle';
import * as Consts from 'shared/Consts';

const useStyles = makeStyles(style);

type Props = {
  authState: string,
};

const Header = (props: Props): React.Element<typeof Container> => {
  const classes = useStyles();
  const { authState } = props;

  const isLoggedIn = authState === Consts.AUTH_STATUS_SIGNED_IN;
  const email = _get(Auth, ['user', 'attributes', 'email'], '');
  const links = [
    ['DashBoard', <ShowChart key="chart" />, '/'],
    ['My Properties', <HomeWork key="homework" />, '/properties'],
    ['Offers', <FileCopy key="offers" />, '/offers'],
  ];

  const renderLinks = () => {
    return links.map((item, key) => {
      const [name, Icon, path] = item;
      return (
        <NavLink
          key={key}
          to={path}
          className={clsx(classes.menuItem, classes.menuItemLink)}
          activeClassName="active"
          exact
        >
          <ListItemIcon className={classes.menuIcon}>{Icon}</ListItemIcon>
          <ListItemText
            primary={
              <span
                className={clsx(classes.menuItemLinkText, classes.menuItemText)}
              >
                {name}
              </span>
            }
          />
        </NavLink>
      );
    });
  };

  const onClickSignOutHandler = async () => {
    await Auth.signOut();
  };

  return (
    <div className={classes.root}>
      {isLoggedIn ? (
        <AppBar position="static" color="default" className={classes.header}>
          <Container className={classes.container} maxWidth="lg">
            <Grid container alignItems="center">
              <Grid item md={1} sm={1} xs={1} className={classes.menuGrid}>
                <ListItem
                  className={classes.logo}
                  href="/dashboard"
                  component="a"
                >
                  <img src={imageLogo} height="40" alt="LogoW" />
                </ListItem>
              </Grid>
              <Grid item md={6} sm={6} xs={6} className={classes.menuGrid}>
                <div className={classes.menuItems}>{renderLinks()}</div>
              </Grid>
              <Grid
                item
                xs={5}
                className={clsx(classes.menuGrid, classes.actionGrid)}
              >
                <div
                  className={clsx(
                    classes.menuEmail,
                    classes.menuItemLink,
                    classes.menuItemText,
                  )}
                >
                  {email}
                </div>
                <div
                  className={clsx(
                    classes.menuItem,
                    classes.menuItemLink,
                    classes.menuUserSignOutBtn,
                  )}
                >
                  <ListItem
                    className={clsx(
                      classes.menuItemText,
                      classes.noHoverBackground,
                    )}
                    disableGutters={true}
                    button
                    component="a"
                    onClick={onClickSignOutHandler}
                  >
                    Sign Out
                  </ListItem>
                </div>
              </Grid>
            </Grid>
          </Container>
        </AppBar>
      ) : (
        <header className="top_header desktop">
          <div className="menu">
            <a href="/" className="menu_btn">
              Home
            </a>
            <a href="/about" className="menu_btn">
              About
            </a>
            <a href="/how-it-works" className="menu_btn">
              How It Works
            </a>
            <a href="/search" className="menu_btn">
              Search
            </a>
            <a href="/soldhomes" className="menu_btn">
              Sold Homes
            </a>
            <a href="/contact" className="menu_btn">
              Contact Us
            </a>
            <a href="/media" className="menu_btn">
              Media
            </a>
          </div>
        </header>
      )}
    </div>
  );
};

export default React.memo(Header);
