import 'react-app-polyfill/ie9';
import React from 'react';
import ReactDOM from 'react-dom';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';
import { NODE_ENV, SENTRY_DSN } from './shared/Consts';
import Dashboard from './containers/NewDashboard/Dashboard';
import PropertyDashboard from './containers/SellerProperty/Dashboard';
import OfferStep from './containers/OfferStep';
import PropertyList from './containers/Property/List';
import OfferList from './containers/SellerOffer/NewOfferList/List';
import * as serviceWorker from './serviceWorker';
import { Provider } from 'react-redux';
import ReduxToastr from 'react-redux-toastr';
import store from './reducer';
import * as Sentry from '@sentry/react';
import { ThemeProvider } from '@material-ui/core/styles';
import { theme } from 'utils/Theme';
import { ApolloProvider } from '@apollo/client';
import { client } from 'dataProvider/client';

import './css/common.css';
import './css/nilehome.css';
import 'react-redux-toastr/lib/css/react-redux-toastr.min.css';
import 'react-block-ui/style.css';
import 'loaders.css/loaders.min.css';

Sentry.init({ dsn: SENTRY_DSN, environment: NODE_ENV });

const WithProvider = () => (
  <ApolloProvider client={client}>
    <Provider store={store}>
      <div>
        <ReduxToastr
          timeOut={4000}
          newestOnTop={false}
          preventDuplicates
          position="top-right"
          getState={state => state.toastr} // This is the default
          transitionIn="fadeIn"
          transitionOut="fadeOut"
          progressBar={false}
          closeOnToastrClick
        />
      </div>
      <ThemeProvider theme={theme}>
        <Router>
          <Switch>
            <Route exact path="/" component={Dashboard} />
            <Route exact path="/offer" component={OfferStep} />
            <Route exact path="/property" component={PropertyDashboard} />
            <Route exact path="/properties" component={PropertyList} />
            <Route exact path="/offers" component={OfferList} />
          </Switch>
        </Router>
      </ThemeProvider>
    </Provider>
  </ApolloProvider>
);
ReactDOM.render(<WithProvider />, document.getElementById('root'));
serviceWorker.unregister();
